


















footer {
  height: 45px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  color: #fff;
  background: #000;
  line-height: 45px;
}
footer .foot-area {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
}
footer .foot-area.money {
  padding-left: 10px;
  color: #f00;
}
footer .foot-area.money .money-des {
  font-size: 12px;
  padding-right: 10px;
  color: #fff;
}
footer .foot-area.peisong {
  font-style: italic;
  color: #808080;
  font-size: 14px;
}
footer .foot-area.goods-submit {
  height: 45px;
  padding: 0 10px;
  background: #999;
  color: #fff;
}
footer .foot-area.goods-submit.goods-submit-active {
  background: #008000;
}
.goods-list {
  height: calc(100vh - 90px);
  overflow: hidden;
}
.goods-list .goodLi {
  padding: 10px 0;
  border-bottom: 1px solid #000;
}
.goods-list .goodLi:first-of-type {
  border-top: 1px solid #000;
}
.goods-list .goodLi:last-of-type {
  border-bottom: none;
}
.goods-list .goodLi:after {
  content: "";
  display: block;
  clear: both;
}
.goods-list .goodLi .left-img {
  float: left;
  width: 40%;
}
.goods-list .goodLi .left-img img {
  width: 100%;
}
.goods-list .goodLi .right-description {
  padding-top: 40px;
  float: left;
  width: 60%;
}
.goods-list .goodLi .right-description .good-name {
  margin-bottom: 5px;
}
.goods-list .goodLi .right-description .good-des {
  font-size: 12px;
  font-style: italic;
}
.goods-list .goodLi .right-description .num-click {
  overflow: hidden;
  padding-top: 10px;
}
.goods-list .goodLi .right-description .num-click .good-price {
  color: #b4282d;
  display: inline-block;
  float: left;
}
.goods-list .goodLi .right-description .num-click .click-area {
  float: right;
}
.goods-list .goodLi .right-description .num-click .click-area .click-icon {
  display: inline-block;
  width: 25px;
  height: 25px;
  text-align: center;
  line-height: 20px;
  border-radius: 50%;
  cursor: pointer;
  border: 1px solid #2bbbd4;
  color: #fff;
  background: #2bbbd4;
}
.goods-list .goodLi .right-description .num-click .click-area .click-icon.goods-disable {
  background: #ddd;
  border-color: #ddd;
  cursor: not-allowed;
}
.goods-list .goodLi .right-description .num-click .click-area .good-num {
  width: 20px;
  text-align: center;
  font-size: 16px;
  outline: none;
  border: 0;
}
.goods-box {
  padding: 0 20px;
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.app-header {
  text-align: center;
  height: 45px;
  line-height: 45px;
}
*{
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

ul,li,ol{
  list-style: none;
}
